___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    background-color: rgb(13, 115, 199);
    color: whitesmoke;
    font-size: 10px;
}

@media only screen and (min-width: 35.5em) {
    html {
        font-size: 12px;
    }
}

@media only screen and (min-width: 48em) {
    html {
        font-size: 14px;
    }
}

@media only screen and (min-width: 64em) {
    html {
        font-size: 16px;
    }
}

@media only screen and (min-width: 80em) {
    html {
        font-size: 18px;
    }
}

@media only screen and (min-width: 120em) {
    html {
        font-size: 20px;
    }
}

@media only screen and (min-width: 160em) {
    html {
        font-size: 22px;
    }
}

@media only screen and (min-width: 240em) {
    html {
        font-size: 24px;
    }
}

body {
    height: 100vh;
}

#game-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;

    width: 100%;
    height: 100%;
}

.game-title {
    grid-column: span 2;
    padding: 1rem;
    justify-self: center;
}

.player-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    font-size: 0.8rem;
}

.board-container {
    display: grid;
}

.cell {
    border: 1px solid black;
    background-color: rgb(58, 248, 255);
}

.board-container[data-active="true"] .cell:hover {
    filter: brightness(0.8);
}

.board-container[data-active="true"] .cell:active {
    transform: scale(1.2);
}

.board-container[data-active="false"] .ship {
    background-color: blue;
}

.miss {
    background-color: black;
}

.hit {
    background-color: red;
}

@keyframes fadein {
    0%{
        opacity: 0;
        top: -10rem;
    }
    100% {
        opacity: 1;
        top: 0;
    }
}

dialog {
    width: 100%;
    height: 100%;
    background-color: rgb(13, 115, 199);
    color: whitesmoke;
}

dialog[open] {
    animation: fadein 1s ease-in forwards;
}

#gameOverDiv {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 4rem;
}

#playAgainButton {
    justify-self: center;
    width: 10rem;
    border-radius: 2rem;
    padding: 1rem;
    background-color: rgba(30, 185, 30, 0.747);
    color: whitesmoke;
}

#gameOverTitle {
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;IAGI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,mCAAmC;IACnC,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;;IAE5B,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,uBAAuB;IACvB,mCAAmC;AACvC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI;QACI,UAAU;QACV,WAAW;IACf;IACA;QACI,UAAU;QACV,MAAM;IACV;AACJ;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mCAAmC;IACnC,iBAAiB;AACrB;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,0BAA0B;IAC1B,4BAA4B;IAC5B,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,0CAA0C;IAC1C,iBAAiB;AACrB;;AAEA;IACI,8BAA8B;IAC9B,gCAAgC;AACpC","sourcesContent":["*,\r\n*::before,\r\n*::after {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nhtml {\r\n    background-color: rgb(13, 115, 199);\r\n    color: whitesmoke;\r\n    font-size: 10px;\r\n}\r\n\r\n@media only screen and (min-width: 35.5em) {\r\n    html {\r\n        font-size: 12px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 48em) {\r\n    html {\r\n        font-size: 14px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 64em) {\r\n    html {\r\n        font-size: 16px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 80em) {\r\n    html {\r\n        font-size: 18px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 120em) {\r\n    html {\r\n        font-size: 20px;\r\n    }\r\n}\r\n\r\n@media only screen and (min-width